.container {
    box-sizing: border-box;
    width: 386px;
    height: 444px;
    padding: 16px;
    margin: 0 auto;
}

.shadow  {
    background-color:  #FFFFFF;
    filter: drop-shadow(0px 4px 35px rgba(168, 172, 176, 0.19));
}

.blockImg {
    position: relative;
    width: 354px;
    height: 330px;
}

.img {
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    position: relative;
}

.img:nth-child(2) {
    object-fit: fill;
}

.hover {
    position: absolute;
    bottom: 52px;
    right: 15px;
    cursor: pointer;
}

.stock {
    font-size: 24px;
    font-weight: 400;
    color:  #8D8F9A;
    position: absolute;
    top: 44%;
    right: 25%;
    left: 25%;
    bottom: 44%;
}

.content {
    width: 354px;
    height: 58px;
    margin-top: 24px;
}

.title {
    font-size: 18px;
    font-weight: 300;
    color: #1D1F22;
    margin: 10px 0 0;
}

.price {
    font-size: 18px;
    color: #1D1F22;
    font-weight: 500;
    margin-top: 10px ;
}

